.forgotPasswordDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background-color: rgb(5, 5, 22);
    color: white;

    .forgotPasswordForm {
        display: flex;
        flex-direction: column;
        padding: 40px;
        width: 500px;
        background-color: rgb(13, 78, 103);
        border-radius: 10px;

        .pageTitle {
            font-size: 1.5rem;
            padding-bottom: 0;
        }

        .subTitle {
            font-size: 0.8rem;
            margin-bottom: 20px;
        }

        .formGroup {
            margin-bottom: 30px;
        }
    }
}
